import React from 'react';
import {useNavigate} from "react-router-dom";

const SurveyFieldsList = (props) => {
    const data = [{
        "field_id": 1,
        "field_name": " ",
        "field_type": "checkbox",
        "field_description": "  ",
        "field_values": {
            "0": " ",
            "1": " ",
            "2": " ",
            "3": " ",
            "100": "  "
        }
    },
        {
            "field_id": 2,
            "field_name": "  ",
            "field_type": "radiobox",
            "field_description": "  ",
            "field_values": [
                "",
                "",
                ",  "
            ]
        },
        {
            "field_id": 3,
            "field_name": "   ",
            "field_type": "file",
            "field_description": "    ",
            "field_values": [
                "d7bbc0ce9327410799450755a8ec1f5d.jpg",
                "757700e0ed874a18abcf5e39c4673a36.jpg",
                "98e0a3678c7f4e5bacd55c7f546d40be.jpg"
            ]
        },
        {
            "field_id": 4,
            "field_name": "  ",
            "field_type": "text",
            "field_description": "    ,   ,   "
        },
        {
            "field_id": 5,
            "field_name": " ",
            "field_type": "checkbox",
            "field_description": " ",
            "field_values": [
                "",
                "",
                " 20x5"
            ]
        }];

    const surveyName = {id: 1, name: '    '}
    const navigate = useNavigate();
    return (
        <>
            <div style={{backgroundColor: '#32465A', color: '#FFFFFF'}}>
                 {surveyName.id}. {surveyName.name}
            </div>
            <div>
                <ul style={{padding: 0, alignItems: 'center'}}>
                    {data.map(x =>
                        <li style={{
                            listStyle: 'none',
                            backgroundColor: '#B2DC58',
                            color: '#162041',
                            borderRadius: '6px',
                            margin: '10px',
                            padding: '10px'
                        }} onClick={() => navigate('/survey-field', {state: x})}>
                            #{x.field_id}. {x.field_name}
                        </li>)}
                </ul>
            </div>
        </>
    );
};

export default SurveyFieldsList;